.mobile-menu-open {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  line-height: 0;
}

.mobile-menu-close-icon {
  width: 8px;
  height: 8px;
  fill: currentColor;
  transition: color var(--transition-duration-func);
}

.mobile-menu-open-icon {
  width: 28px;
  height: 28px;
  fill: #111;
}

.mobile-menu-open-icon:hover {
  fill: var(--accent-color);
}

.mobile-menu-open .icon-cross {
  display: none;
}

.mobile-menu-open.is-open .icon-cross {
  display: block;
}

@media screen and (min-width: 768px) {
  .mobile-menu-open {
    display: none;
  }
}

.header__link {
  height: 28px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin-right: 40px;
  display: flex;
}

.header__logo-container {
  padding-top: 22px;
  padding-bottom: 22px;
}

.header__container {
  min-width: 320px;
  height: 68px;
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__container {
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .header__container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.header__nav-logo {
  height: 64px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__nav-logo {
    height: 72px;
  }
}

.header__nav {
  align-items: center;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .header__nav {
    display: none;
  }
}

.nav__list-home {
  height: 34px;
  text-transform: uppercase;
  letter-spacing: -.04em;
  color: var(--header-bg-color-theme-dark);
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  border-radius: 36px;
  outline: none;
  gap: 4px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: flex;
}

.nav__list-home:hover .header__nav-link {
  color: var(--accent-color);
}

.nav__list-home:hover .header__nav-icon {
  fill: var(--accent-color);
  stroke: var(--accent-color);
}

.current {
  background-color: var(--hover-color);
  color: #111;
  font-weight: 700;
}

.current .header__nav-icon {
  fill: #111;
}

.header__menu-login {
  width: 184px;
  height: 48px;
  background-color: var(--accent-color);
  color: var(--header-bg-color-theme-light);
  cursor: pointer;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 14px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: flex;
}

.header__menu-login:hover, .header__menu-login:focus {
  color: var(--hover-color);
}

@media screen and (max-width: 767px) {
  .btn-logout {
    display: none;
  }
}

.header__theme-switcher {
  justify-content: center;
  align-items: center;
  gap: 24px;
  display: flex;
}

.switcher {
  width: 40px;
  height: 20px;
  display: block;
  position: relative;
}

.switcher-input {
  height: 0;
  width: 0;
  opacity: 0;
}

.switcher-slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 30px;
  position: absolute;
  top: 8.33%;
  bottom: 8.33%;
  left: 0;
  right: 0;
}

.switcher-slider:before {
  content: "";
  cursor: pointer;
  height: 15px;
  width: 15px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 1px;
  bottom: 12.5%;
  left: 2.5%;
  right: 52.5%;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.switcher-input:checked + .switcher-slider:before {
  transform: translateX(23px);
}

.header__btn-user {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 767px) {
  .header__btn-user {
